.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header{
  background-image: linear-gradient(rgba(255,255,255,0.5),rgba(0, 0, 0, 0)), url(./assets/ferry.jpg);
/* rgba(255,255,255,0) */
  /* background-image: linear-gradient(rgba(0,0,0,0.1),rgba(0,0,0,0.1)), url(./assets/ferry.jpg); */
  background-size: cover;
  background-position: center;
  height:100vh;
  background-attachment: fixed;
}
